export default {
	components: {
		newsNavBar,
		commonList,
		loadMore
	},
	data() {
		return {
			tabIndex: 1,
			swiperheight: 500,
			tabBars: [{
					name: "关注",
					id: "guanzhu"
				},
				{
					name: "话题",
					id: "huati"
				}
			],
			guanzhu: {
				loadtext: '上拉加载更多',
				list: [
					//文字
					{
						userpic: "../../static/demo/userpic/8.jpg",
						username: "昵称",
						sex: 0, // 0男，1女
						age: 25,
						isguanzhu: false,
						title: "我是标题",
						titlepic: "",
						video: false,
						share: false,
						path: "深圳 龙岗",
