						sex: 0, // 0男，1女
						age: 25,
						isguanzhu: false,
						title: "我是标题",
						titlepic: "../../static/demo/datapic/28.jpg",
						video: false,
						share: false,
						path: "深圳 龙岗",
						sharenum: 20,
