					{
						userpic: "../../static/demo/userpic/8.jpg",
						username: "昵称",
						sex: 1, // 0男，1女
						age: 25,
						isguanzhu: false,
						title: "我是标题",
						titlepic: "../../static/demo/datapic/28.jpg",
