	methods: {
		//点击事件
		changeTab(index) {
			this.tabIndex = index
		},
		//滑动事件
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		loadmore() {
			//如果当前状态是还在加载中，或没有更多数据，直接返回
			if (this.guanzhu.loadtext != "上拉加载更多") {
				return;
			}
			//修改状态
			this.guanzhu.loadtext = "加载中";
			//获取数据
			setTimeout(() => {
				//获取完成
				let obj = {
					userpic: "../../static/demo/userpic/8.jpg",
					username: "昵称",
					sex: 1, // 0男，1女
					age: 25,
