						long: "2:17"
					},
					share: false,
					path: "深圳 龙岗",
					sharenum: 20,
					commentnum: 30,
					goodnum: 20
				};
				this.guanzhu.list.push(obj);
				this.guanzhu.loadtext = "上拉加载更多";
			}, 2000);
			//如果没有数据显示
			// this.guanzhu.loadtext = "没有更多数据";
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100);
				this.swiperheight = height;
			}
		})
	},
}
